import { Component, Input, OnChanges, OnInit, Output } from "@angular/core";

import { Category } from "../categories/category";
import { CategoriesService } from "../categories/categories.service";
import { setTimeout } from "timers";

@Component({
  selector: 'swu-breadcrumbs',
  templateUrl: './breadcrumbs.component.html',
  styleUrls: ['./breadcrumbs.component.css']
})

export class BreadCrumbsComponent implements OnChanges, OnInit {
  public crumbs: Category[]=[];
  private allCategories: Category[];
  public activeCrumb: Category;
  @Input() id: number;

  constructor(private categoryService: CategoriesService) {
  }

  ngOnChanges() {
   if (this.allCategories) {
       this.pushCrumbs();
    }
  }

  ngOnInit() {
    this.categoryService.getCategories().subscribe(value => {
      this.allCategories = value;
      this.pushCrumbs();
    });
  }

  getCategorybyId(id: number): Category {
    return this.allCategories.find((temp: Category) =>
      temp.id == id);
  }

  pushCrumbs() {
    let id: number = this.id;
    var crumb: Category;
    this.crumbs = [];
    this.activeCrumb = this.getCategorybyId(this.id);

    crumb = this.getCategorybyId(id);
    while (crumb.parentId != 0 && crumb.parentId != undefined) {
      id = crumb.parentId;
      crumb = this.getCategorybyId(id);
      this.crumbs.unshift(crumb);
    }
  }

}
