import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { AppRoutingModule } from './app-routing.module';

import { AppComponent } from './app.component';
import { CategoriesListComponent } from './categories/categories-list.component';
import { SideMenuComponent } from './categories/side-menu.component';
import { DisplayMaterialComponent } from './materials/display-material.component';
import { HomeComponent } from './home/home.component';
import { BreadCrumbsComponent } from './shared/breadcrumbs.component';
import { PaginationComponent } from './shared/pagination.component';

@NgModule({
  declarations: [
    AppComponent,
    CategoriesListComponent,
    SideMenuComponent,
    DisplayMaterialComponent,
    HomeComponent,
    BreadCrumbsComponent,
    PaginationComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    AppRoutingModule
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
