import { Component, OnInit, Input } from '@angular/core';

import { Category } from "./category";
import { CategoriesService } from './categories.service';


@Component({
  selector: 'swu-menu',
  templateUrl: './categories-list.component.html',
  styleUrls: ['./categories-list.component.css']
 })



export class CategoriesListComponent implements OnInit{
  activeId: number;
  @Input() categoriesList: Category[];
  public mainCategoriesList: Category[];
  public clicked:boolean = false;

  constructor(private categoriesService: CategoriesService) { }

  ngOnInit():void {
    this.categoriesService.getCategories().subscribe(value => {
       this.mainCategoriesList = value.filter((temp: Category) => {
        return temp.parentId === 0 || temp.parentId == undefined;
      })
    });
  }

  sendActiveId(id: number) {
    if (this.activeId != id) { this.activeId = id; } else this.activeId = undefined;
  }

   childCategories(id: number): Category[] {
    return this.categoriesList.filter((temp: Category) => {
      return temp.parentId == id;
    }
      );
  }


  

}
