import { OnInit, OnChanges, Component, Input } from "@angular/core";
import { ActivatedRoute } from '@angular/router';

import { Material } from "./material";
import { MaterialService } from "./material.service";
import { Subscription } from "rxjs";

@Component({
  templateUrl: './display-material.component.html',
  styleUrls: ['./display-material.component.css']
})


export class DisplayMaterialComponent implements OnInit, OnChanges {
  _id: number;
  itemsOnPage: number = 5;
  currentPage: number = 1;
  public materialList: Material[] | undefined;
  public slicedList: Material[];
  private subscription: Subscription;

  constructor(
    private router: ActivatedRoute,
    private materialService: MaterialService) {
    this.subscription = this.router.params.subscribe(
      params => this.id = params['id']);
  }

  ngOnInit() {
    this.getMaterialForCategoryId();

  }

  ngOnChanges() {

  }

  set id(id: number) {
    this._id = id;
    this.getMaterialForCategoryId();

  }

  get id() {
    return this._id;
  }

  getMaterialForCategoryId() {
    this.materialService.getMaterials().subscribe(
      materials => {
        this.materialList = materials.filter(
          (temp: Material) => {
            return Number(temp.categoryId) == this.id;
          });
        this.setSlicedList(1);
      }
    );
  }

  setSlicedList(page) {
    let start = (page - 1) * this.itemsOnPage;
    let end = start + this.itemsOnPage;
    this.slicedList = this.materialList.slice(start, end);
  }

  changeList(page: number) {
      this.setSlicedList(page);
  }







}
